export default {
  created: function () {
    this.$store.dispatch('initPathHistory', window.location.hash.substring(1))
    window.lms = this.$store.getters.getLms
    window.onbeforeunload = function () {
      window.lms.exit()
    }
    window.onunload = function () {
      window.lms.exit()
    };
  }
}