export default {
  /* eslint-disable */
  langPath: function (path, lang) {
    if (path === '/') {
      return lang
    } else {
      return lang + path
    }
  },
  removeLangPath: function (path, lang) {
    if(path === lang) {
      return '/'
    } else if (path.indexOf(lang) === 0) {
      return path.replace(lang, '')
    } else {
      return '***'
    }
  },
  clonePage: function (page, lang) {
    let newPage = {}
    newPage.title = page.title
    newPage.path = this.removeLangPath(page.path, lang)
    newPage.component = page.component
    newPage.connections = []
    for(let i = 0; i < page.connections.length; i++) {
      let newConnection = {}
      newConnection.backwardTransition = page.connections[i].backwardTransition
      newConnection.forwardTransition = page.connections[i].forwardTransition
      newConnection.gate = page.connections[i].gate
      newConnection.path = page.connections[i].path
      newPage.connections.push(newConnection)
    }
    return newPage
  }
}