<template>
    <div class="multiple-choice" :class="{active: showFeedback}">
        <div class="question" >
            <div class="question-header">
                <p class="instructions" v-html="instructions"></p>
                <p class="count">{{count.position}} of {{count.total}}</p>
            </div>
            <p class="query" v-html="query"></p>
            <div class="distractors">
                <div class="distractor-holder" v-for="(distractor, index) in distractors" :key="index" >
                    <div v-if="options.multiSelect"  @click="onMultiSelect(distractor)" class="distractor" :class="{selected: distractor.selected, lock:lock, highlight: (distractor.correct && options.highlightFeedback && lock) || (distractor.correct && highlightReview)}">
                        <input type="checkbox" name="q" v-model="distractor.selected">
                        <svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" width="100%" height="100%" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid meet" ng-show="slide1.multi">
                            <path id="border" d="M0,0v100h100V0H0z M90,90H10V10h80V90z"/>
                            <rect id="check" x="25" y="25" width="50" height="50"  v-show="distractor.selected"/>
                        </svg>
                        <label v-html="distractor.label"></label>
                    </div>
                    <div v-else @click="onSelect(distractor)" class="distractor" :class="{selected: distractor.selected, lock:lock, highlight: (distractor.correct && options.highlightFeedback && lock) || (distractor.correct && highlightReview)}">
                        <input type="checkbox" name="q"  v-model="distractor.selected">
                        <svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" width="100%" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid meet" ng-hide="slide1.multi">
                            <path id="ring" d="M50,0C22.4,0,0,22.4,0,50c0,27.6,22.4,50,50,50s50-22.4,50-50C100,22.4,77.6,0,50,0z M50,90c-22.1,0-40-17.9-40-40c0-22.1,17.9-40,40-40s40,17.9,40,40C90,72.1,72.1,90,50,90z"/>
                            <circle id="dot" cx="50" cy="50" r="30" v-show="distractor.selected"/>
                        </svg>
                        <label v-html="distractor.label"></label>
                    </div>
                </div>
            </div>
        </div>
        <div class="feedback">
            <div class="correct" v-html="feedbackDisplay" v-if="answerCorrect"></div>
            <div class="incorrect" v-html="feedbackDisplay" v-else></div>
        </div>
    </div>
</template>

<script>
import Randomize from 'simple-core/mixins/RandomizeMixin'
export default {
  mixins: [Randomize],
  props: {
    count: {
      type: Object
    },
    question: {
      type: Object
    },
    lock: {
      type: Boolean
    },
    showFeedback: {
      type: Boolean
    }
  },
  name: 'multiple-choice',
  created () {
    this.id = this.question.id
    this.instructions = this.question.content.instructions
    this.query = this.question.content.query
    this.options = this.question.options
    this.distractors = (this.options.randomizeDistractors) ? this.randomize(this.question.content.distractors) : this.question.content.distractors
    this.feedback = this.question.content.feedback
  },
  data () {
    return {
      id: '',
      distractors: [],
      instructions: '',
      feedback: {},
      query: '',
      options: {},
      feedbackDisplay: '',
      answerCorrect: false
    }
  },
  methods: {
    onSelect (item) {
      if (!this.lock) {
        item.selected = !item.selected
        for (let i = 0; i < this.distractors.length; i++) {
          if (this.distractors[i] !== item) {
            this.distractors[i].selected = false
          }
        }
        this.setFeedback()
      }
    },
    onMultiSelect (item) {
      if (!this.lock) {
        item.selected = !item.selected
        this.setFeedback()
      }
    },
    setFeedback () {
      var grade = this.grade()
      if (grade === false) {
        this.$emit('submittable', false)
      } else {
        this.$emit('submittable', true)
      }
      if (this.options.distractorFeedback) {
        this.feedbackDisplay = this.feedback[grade.details[0]]
      } else {
        if (grade.correct) {
          this.feedbackDisplay = this.feedback.correct
        } else {
          this.feedbackDisplay = this.feedback.incorrect
        }
      }
      this.answerCorrect = grade.correct
    },
    grade (interaction = false) {
      let questionCorrect = true
      let correctResponse = []
      let correctId = []
      let learnerResponse = []
      let learnerId = []
      let questionDetails = []
      if (this.options.multiSelect) {
        for (let i = 0; i < this.distractors.length; i++) {
          if (this.distractors[i].correct && this.distractors[i].selected) {
            questionDetails.push(this.distractors[i].id)
            correctResponse.push(this.distractors[i].label)
            learnerResponse.push(this.distractors[i].label)
            correctId.push(this.distractors[i].id)
            learnerId.push(this.distractors[i].id)
          } else if (this.distractors[i].correct && !this.distractors[i].selected) {
            questionCorrect = false
            questionDetails.push(this.distractors[i].id)
            correctResponse.push(this.distractors[i].label)
            correctId.push(this.distractors[i].id)
          } else if (!this.distractors[i].correct && this.distractors[i].selected) {
            questionCorrect = false
            learnerResponse.push(this.distractors[i].label)
            learnerId.push(this.distractors[i].id)
          }
        }
      } else {
        for (let i = 0; i < this.distractors.length; i++) {
          if (this.distractors[i].correct && this.distractors[i].selected) {
            questionCorrect = true
            questionDetails.push(this.distractors[i].id)
            correctResponse.push(this.distractors[i].label)
            learnerResponse.push(this.distractors[i].label)
            correctId.push(this.distractors[i].id)
            learnerId.push(this.distractors[i].id)
          } else if (!this.distractors[i].correct && this.distractors[i].selected) {
            questionCorrect = false
            questionDetails.push(this.distractors[i].id)
            learnerResponse.push(this.distractors[i].label)
            learnerId.push(this.distractors[i].id)
          } else if (this.distractors[i].correct && !this.distractors[i].selected) {
            questionCorrect = false
            correctResponse.push(this.distractors[i].label)
            correctId.push(this.distractors[i].id)
          }
        }
      }
      if (questionDetails.length === 0) {
        return false
      }
      if (interaction) {
        if (this.options.recordInteraction) {
          this.$store.dispatch('setInteraction', { id: this.id, result: questionCorrect, description: this.query, type: 'choice', correctResponse: correctResponse.join(','), correctId: correctId.join(','), learnerResponse: learnerResponse.join(','), learnerId: learnerId.join(','), weight: 1 })
        }
      }
      return { correct: questionCorrect, details: questionDetails }
    },
    clear () {
      for (let i = 0; i < this.distractors.length; i++) {
        this.distractors[i].selected = false
      }
    }
  },
  computed: {
    highlightReview () {
      return this.$store.getters.getHighlightReview
    }
  }
}
</script>

<style lang="scss" scoped>
    .multiple-choice{
        display: -ms-grid;
        display: grid;
        -ms-grid-columns: 100%;
        grid-template-columns: 100%;
        .question-header{
            display: -ms-grid;
            display: grid;
            -ms-grid-columns: 1fr 10rem;
            grid-template-columns: 1fr 10rem;
            .instructions{
                -ms-grid-column: 1;
                grid-column: 1;
                -ms-grid-row: 1;
                grid-row: 1;
                font-style: italic;
            }
            .count{
                -ms-grid-column: 2;
                grid-column: 2;
                -ms-grid-row: 1;
                grid-row: 1;
                justify-self: end;
            }
        }
        .question, .feedback{
            -ms-grid-column: 1;
            grid-column: 1;
            -ms-grid-row: 1;
            grid-row: 1;
            overflow: hidden;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            transition: transform 0.5s ease-in-out;
            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                transition: opacity 0.5s, transform 0.5s ease-in-out;
            }
        }
        .question{
            backface-visibility: hidden;
            transform-style: preserve-3d;
            transform: perspective(800px) rotateX(0deg);
            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                opacity: 1;
            }
            padding: 2rem 2rem 2rem 2rem;
            padding-bottom: 3rem;
            background-color: white;
            color: black;
            .distractor{
                transition: background 0s 0s;
                position:relative;
                display: -ms-grid;
                display: grid;
                margin-bottom: 1rem;
                -ms-grid-columns: 1.5rem 1fr;
                grid-template-columns: 1.5rem 1fr;
                input {
                    clip: rect(0,0,0,0);
                    clip: rect(0 0 0 0);
                    -ms-grid-column: 1;
                    grid-column: 1;
                    -ms-grid-row: 1;
                    grid-row: 1;
                    position:absolute;
                    cursor: pointer;
                }
                svg {
                    -ms-grid-column: 1;
                    grid-column: 1;
                    -ms-grid-row: 1;
                    grid-row: 1;
                    width: 1.5rem;
                    height: 1.5rem;
                    fill: color(text);
                    cursor: pointer;
                }
                label {
                    -ms-grid-column: 2;
                    grid-column: 2;
                    -ms-grid-row: 1;
                    grid-row: 1;
                    padding-top: 0.25rem;
                    padding-left: 0.5rem;
                    cursor: pointer;
                }
                &.lock{
                    input, svg, label{
                        cursor: default;
                    }
                }
                &.highlight{
                    background: #ffff00;
                    transition: background 0.25s 0.5s;
                }
            }
        }
        .feedback{
            backface-visibility: hidden;
            transform-style: preserve-3d;
            transform: perspective(800px) rotateX(180deg);
            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                opacity: 0;
            }
            display: -ms-grid;
            display: grid;
            -ms-grid-columns: 100%;
            grid-template-columns: 100%;
            .correct, .incorrect{
                padding: 2rem 2rem 2rem 2rem;
                -ms-grid-column: 1;
                grid-column: 1;
                -ms-grid-row: 1;
                grid-row: 1;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                padding-bottom: 3rem;
            }
            .correct{
                background-color: darkgreen;
                color: white;
            }
            .incorrect{
                background-color: red;
                color: white;
            }
        }
        &.active {
            .question {
                transform: perspective(800px) rotateX(-179.9deg);
                @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                    opacity: 0;
                }
            }
            .feedback {
                transform: perspective(800px) rotateX(0);
                @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                    opacity: 1;
                }
            }
        }
    }
</style>