export default{
  methods: {
    randomize (array) {
      let myArray = array.slice(0)
      for (var i = myArray.length - 1; i > 0; i--) {
        var j = Math.floor(Math.random() * (i + 1));
        var temp = myArray[i];
        myArray[i] = myArray[j];
        myArray[j] = temp;
      }
      return myArray
    }
  }
}