import {SCORM} from 'pipwerks-scorm-api-wrapper';

export default class {
  constructor () {
    this.lmsConnected = false
  }
  init () {
    this.lmsConnected = SCORM.init();
    this.isClosed = false
  }
  exit () {
    if (!this.isClosed) {
      this.isClosed = true
      SCORM.quit()
    }
  }
  isConnected () {
    return this.lmsConnected
  }
  setComplete () {
    if (this.lmsConnected) {
      SCORM.set('cmi.core.lesson_status', 'completed')
    }
  }
  setBookmark (location) {
    if (this.lmsConnected) {
      SCORM.set("cmi.core.lesson_location", location)
    }
  }
  getBookmark () {
    if (this.lmsConnected) {
      return SCORM.get("cmi.core.lesson_location")
    }
  }
  setStoredState (completionData) {
    if (this.lmsConnected) {
      SCORM.set("cmi.suspend_data", JSON.stringify(completionData))
    }
  }
  getStoredState () {
    if (this.lmsConnected) {
      let completion = SCORM.get("cmi.suspend_data")
      if (completion.length > 0) {
        let completionObj = JSON.parse(completion)
        return completionObj
      } else{
        return null
      }
    }
  }
  setScore (raw, min, max, scaled) {
    if (this.lmsConnected) {
      SCORM.set("cmi.core.score.raw", raw)
      SCORM.set("cmi.core.score.min", min)
      SCORM.set("cmi.core.score.max", max)
    }
  }
  setInteraction (id, result, description, type, correctResponse, correctId, learnerResponse, learnerId, weight) {
    if (this.lmsConnected) {
      let count = SCORM.get("cmi.interactions._count")
      SCORM.set("cmi.interactions." + count + ".id", id)
      SCORM.set("cmi.interactions." + count + ".result", (result) ? 'correct' : 'incorrect')
      SCORM.set("cmi.interactions." + count + ".type", type)
      SCORM.set("cmi.interactions." + count + ".correct_responses.0.pattern", correctId)
      SCORM.set("cmi.interactions." + count + ".student_response", learnerId)

      function ISODateString() {
        let d = new Date()
        function pad(n) {
          return n < 10 ? '0' + n : n
        }
        let mydate = pad(d.getUTCHours()) + ':' + pad(d.getUTCMinutes()) + ':' + pad(d.getUTCSeconds())
        return mydate
      }

      SCORM.set("cmi.interactions." + count + ".time", ISODateString())
      SCORM.set("cmi.interactions." + count + ".weighting", weight)
    }
  }
}