<template>
<div class="editing-panel">
    <!--<button class="p-btn" v-on:click="pSelected" title="P"></button>
    <button class="h1-btn" v-on:click="h1Selected" title="H1"></button>
    <button class="h2-btn" v-on:click="h2Selected" title="H2"></button>
    <button class="h3-btn" v-on:click="h3Selected" title="H3"></button>
    <button class="b-btn" v-on:click="bSelected" title="B"></button>
    <button class="i-btn" v-on:click="iSelected" title="I"></button>
    <button class="u-btn" v-on:click="uSelected" title="U"></button>
    <button class="s-btn" v-on:click="sSelected" title="S"></button>-->
    <button class="answers-btn" v-on:click="answersSelected" :class="{active: answerToggle}" title="Show Answers"></button>
    <button class="gating-btn" v-on:click="gatingSelected" :class="{active: gatingToggle}" title="Ignore Gating"></button>
    <button class="settings-btn" v-on:click="settingsSelected" title="Course Settings"></button>
    <button class="save-btn" v-on:click="saveSelected" title="Save"></button>
    <div class="handle-btn" v-on:click="handleSelected" title="::::::::::::::::::"></div>
</div>
</template>

<script>
import contentEdit from 'ContentEdit/build/content-edit.js'
import Draggable from 'gsap/Draggable'
export default {
  name: 'edit-panel',
  data () {
    return {
      tainted: false
    }
  },
  created () {
    var scope = this
    contentEdit.Root.get().bind('taint', function (element) {
      scope.tainted = true
    })
  },
  mounted () {
    Draggable.create(this.$el, {type:"x,y", bounds:"#app", edgeResistance:0.65, throwProps:true});
  },
  computed: {
    answerToggle () {
      return this.$store.getters.getHighlightReview
    },
    gatingToggle () {
      return this.$store.getters.getIgnoreGating
    }
  },
  methods: {
    pSelected () {

    },
    h1Selected () {

    },
    h2Selected () {

    },
    h3Selected () {

    },
    bSelected () {

    },
    iSelected () {

    },
    uSelected () {

    },
    sSelected () {

    },
    answersSelected () {
      this.$store.commit('setHighlightReview', !this.$store.getters.getHighlightReview)
    },
    gatingSelected () {
      this.$store.commit('setIgnoreGating', !this.$store.getters.getIgnoreGating)
    },
    settingsSelected () {
      window.open('http://localhost:8081/simple/settings', '_settings')
    },
    saveSelected () {
      if (!this.tainted) {
        console.log('Nothing has changed yet')
      } else {
        let saveSignal = this.$store.getters.saveSignal
        saveSignal.dispatch()
      }
    },
    handleSelected () {

    },
  }
}
</script>

<style lang="scss" scoped>
.editing-panel{
    z-index:100;
    position: absolute;
    top: 0;
    right: 0;
    display:grid;
    background: #cccccc;
    grid-gap: 1px;
    grid-template-columns: 3rem 3rem;
    grid-template-rows: 3rem 3rem 3rem 3rem 3rem;
    /* grid-template-areas:
            "p h1"
            "h2 h3"
            "b i"
            "u s"
            "settings settings"
            "save save"
            "handle handle"; */
    grid-template-areas:
            "answers answers"
            "gating gating"
            "settings settings"
            "save save"
            "handle handle";
    button, .handle-btn{
        position: relative;
        appearance: none;
        cursor:pointer;
        background-color:#eeeeee;
        font-size:0.80em;
        color:#666666;
        line-height: 1em;
        border: 1px solid #e9e9e9;
        height:100%;
        justify-self: stretch;
        &:focus{
            outline: none;
        }
        &:before{
            transition: border-width 0.3s ease;
            content:'';
            position:absolute;
            left: 1px;
            right: 1px;
            top: 1px;
            bottom: 1px;
            border: 1px solid #f6f6f6;
            z-index:10;
        }
        &:hover:before{
            content:'';
            border-width: 1.42rem;
        }
        &:after{
            content: attr(title);
            color:#666666;
            position:absolute;
            left: 0;
            right: 0;
            top: 1rem;
            bottom: 0;
            z-index:12;
        }
    }
    .p-btn{
        grid-area: p;
    }
    .h1-btn{
        grid-area: h1;
    }
    .h2-btn{
        grid-area: h2;
    }
    .h3-btn{
        grid-area: h3;
    }
    .b-btn{
        grid-area: b;
    }
    .i-btn{
        grid-area: i;
    }
    .u-btn{
        grid-area: u;
    }
    .s-btn{
        grid-area: s;
    }
    .answers-btn{
        grid-area: answers;
        &.active{
            background-color: #00c000;
            border: 1px solid #00a000;
            &:before{
                transition: border-width 0.3s ease;
                border: 1.42rem solid #00b000;
            }
            &:after{
                color: white;
            }
        }
    }
    .gating-btn{
        grid-area: gating;
        &.active{
            background-color: #00c000;
            border: 1px solid #00a000;
            &:before{
                transition: border-width 0.3s ease;
                border: 1.42rem solid #00b000;
            }
            &:after{
                color: white;
            }
        }
    }
    .settings-btn{
        grid-area: settings;
    }
    .save-btn{
        grid-area: save;
    }
    .handle-btn{
        grid-area: handle;
        text-align: center;
    }
}
</style>