import { courseQuestions, courseQuizzes, coursePools, courseQuestionRecords, courseQuizRecords } from '@/contents/questions'

export default {
  state: {
    highlightReview: false,
    questions: courseQuestions,
    quizzes: courseQuizzes,
    pools: coursePools,
    questionRecords: courseQuestionRecords,
    quizRecords: courseQuizRecords
  },
  actions: {

  },
  getters: {
    getHighlightReview (state) {
      return state.highlightReview
    },
    getQuizPosition (state) {
      return function (quizName) {
        if (state.quizzes.hasOwnProperty(quizName)) {
          return state.quizRecords[quizName].position
        } else {
          return false
        }
      }
    },
    getQuestionList (state) {
      return function (quiz) {
        if (typeof quiz === 'object') {
          if (state.quizRecords.hasOwnProperty(quiz.name)) {
            if (state.quizRecords[quiz.name].questionLists.length - 1 < quiz.version) {
              return false
            }
            return state.quizRecords[quiz.name].questionLists[quiz.version]
          } else {
            return false
          }
        } else {
          if (state.quizRecords.hasOwnProperty(quiz)) {
            if (state.quizRecords[quiz].questionLists.length === 0) {
              return false
            }
            return state.quizRecords[quiz].questionLists[state.quizRecords[quiz].questionLists.length - 1]
          } else {
            return false
          }
        }
      }
    },
    getQuestionHistory (state) {
      return function (question) {
        return state.questionRecords[question]
      }
    },
    getQuestion (state, getters, rootState) {
      return function (id) {
        let lang = rootState.core.defaultLanguage.substr(1)
        return state.questions[lang][id]
      }
    },
    getQuiz (state) {
      return function (quizName) {
        if (state.quizzes.hasOwnProperty(quizName)) {
          return state.quizzes[quizName]
        } else {
          return false
        }
      }
    },
    getPool (state) {
      return function (poolName) {
        if (state.pools.hasOwnProperty(poolName)) {
          return state.pools[poolName].slice(0)
        } else {
          return false
        }
      }
    },
    getPoolHistory (state) {
      return function (poolName) {
        if (state.pools.hasOwnProperty(poolName)) {
          let pool = state.pools[poolName]
          let poolArray = []
          for (let i = 0; i < pool.length; i++) {
            poolArray.push({ name: pool[i], history: state.questionRecords[pool[i]] })
          }
          return poolArray
        } else {
          return false
        }
      }
    },
    getQuizHistory (state) {
      return function (quizName) {
        if (state.quizzes.hasOwnProperty(quizName)) {
          return state.quizRecords[quizName].questionLists
        } else {
          return false
        }
      }
    },
    isQuizInProgress (state) {
      return function (quizName) {
        if (state.quizRecords.hasOwnProperty(quizName)) {
          if (state.quizRecords[quizName].position !== -1) {
            return true
          } else {
            return false
          }
        }
      }
    }
  },
  mutations: {
    setHighlightReview (state, n) {
      state.highlightReview = n
    },
    setQuizPosition (state, n) {
      if (state.quizzes.hasOwnProperty(n.name)) {
        state.quizRecords[n.name].position = n.position
      }
    },
    setQuestionList (state, n) {
      if (state.quizzes.hasOwnProperty(n.name)) {
        state.quizRecords[n.name].questionLists.push(n.list)
      }
    },
    setQuestionHistory (state, n) {
      if (state.questionRecords.hasOwnProperty(n.name)) {
        state.questionRecords[n.name].push(n.history)
      }
    },
    loadQuestionRecords (state, n) {
      state.questionRecords = n
    },
    loadQuizRecords (state, n) {
      state.quizRecords = n
    },
    clearQuestionHistory (state) {
      for (var question in state.questionRecords) {
        if (state.questionRecords.hasOwnProperty(question)) {
          state.questionRecords[question] = []
        }
      }
    },
    clearQuizHistory (state) {
      for (var quiz in state.quizRecords) {
        if (state.quizRecords.hasOwnProperty(quiz)) {
          state.quizRecords[quiz].position = -1
          state.quizRecords[quiz].questionLists = []
        }
      }
    }
  }
}