import pages from '@/contents/pages'
import router from '@/router'
import completion from '@/store/completion'
import groups from '@/store/groups'
import strings from '@/contents/strings'
import {defaultLanguage, title, availableLanguages} from '@/../settings/publish-values'
import utils from './core-utils'
import coupler from '@/store/lmsCoupler'

export default {
  state: {
    lms: new coupler(),
    bookmark: '',
    pages: pages,
    completion: completion,
    courseTitle: title,
    pathHistory: [],
    customSuspendData: {},
    defaultLanguage: '/' + defaultLanguage,
    availableLanguages: availableLanguages,
    groups: groups,
    strings: strings,
    courseComplete: false,
    transitionType: '',
    ignoreGating: false,
    overrideTransition: false
  },
  actions: {
    navByPath: function (context, path) {
      let state = context.state
      let commit = context.commit
      let getters = context.getters
      let dispatch = context.dispatch
      let currentPage = getters.getPage()
      let navigate = false
      if (currentPage.path === path) {
        commit('setTransitionType', 'none')
        router.push(utils.langPath(path, state.defaultLanguage))
        dispatch('sendStoredState')
      } else {
        for (let i = 0; i < currentPage.connections.length; i++) {
          let connection = currentPage.connections[i]
          if (connection.path === path) {
            if (connection.gate.length === 0 || state.ignoreGating) {
              if (!state.overrideTransition) {
                commit('setTransitionType', connection.forwardTransition)
              }
              router.push(utils.langPath(connection.path, state.defaultLanguage))
              commit('setPathHistory', connection.path)
              navigate = true
            } else {
              if (connection.gate.indexOf('/') !== -1) {
                //page
                for (var j = 0; j < state.completion.length; j++) {
                  if (state.completion[j].path === connection.gate) {
                    navigate = true
                    if(state.completion[j].complete === 1) {
                      if (!state.overrideTransition) {
                        commit('setTransitionType', connection.forwardTransition)
                      }
                      router.push(utils.langPath(connection.path, state.defaultLanguage))
                      commit('setPathHistory', connection.path)
                    }
                  }
                }
              } else {
                //group
                let groupCompletion = getters.getGroupCompletion(connection.gate)
                navigate = true
                if (groupCompletion === 1) {
                  if (!state.overrideTransition) {
                    commit('setTransitionType', connection.forwardTransition)
                  }
                  router.push(utils.langPath(connection.path, state.defaultLanguage))
                  commit('setPathHistory', connection.path)
                }
              }
            }
          }
        }
        if (!navigate) {
          router.push(utils.langPath(path, state.defaultLanguage))
          commit('setPathHistory', path)
        }
        dispatch('sendStoredState')
      }
    },
    navByConnection: function (context, index) {
      let state = context.state
      let commit = context.commit
      let getters = context.getters
      let dispatch = context.dispatch
      let currentPage = getters.getPage()
      if (currentPage.connections.length > index) {
        let connection = currentPage.connections[index]
        if (connection.gate.length === 0 || state.ignoreGating) {
          if (!state.overrideTransition) {
            commit('setTransitionType', connection.forwardTransition)
          }
          router.push(utils.langPath(connection.path, state.defaultLanguage))
          commit('setPathHistory', connection.path)
        } else {
          if (connection.gate.indexOf('/') !== -1) {
            //page
            for (var j = 0; j < state.completion.length; j++) {
              if (state.completion[j].path === connection.gate) {
                if(state.completion[j].complete === 1) {
                  if (!state.overrideTransition) {
                    commit('setTransitionType', connection.forwardTransition)
                  }
                  router.push(utils.langPath(connection.path, state.defaultLanguage))
                  commit('setPathHistory', connection.path)
                }
              }
            }
          } else {
            //group
            let groupCompletion = getters.getGroupCompletion(connection.gate)
            if (groupCompletion === 1) {
              if (!state.overrideTransition) {
                commit('setTransitionType', connection.forwardTransition)
              }
              router.push(utils.langPath(connection.path, state.defaultLanguage))
              commit('setPathHistory', connection.path)
            }
          }
        }
      }
      dispatch('sendStoredState')
    },
    navByGroup: function (context, title) {
      let state = context.state
      let dispatch = context.dispatch
      let getters = context.getters
      let group = getters.getGroup(title)
      for (let i = 0; i < group.pages.length; i++) {
        let page = group.pages[i]
        if (getters.getPageCompletion(page) < 1) {
          dispatch('navByPath', page)
          return
        }
      }
      dispatch('navByPath', group.pages[0])
    },
    navBack: function (context) {
      let state = context.state
      let commit = context.commit
      let getters = context.getters
      let dispatch = context.dispatch
      let navigate = false
      let currentPath = state.pathHistory[state.pathHistory.length - 1]
      if (state.pathHistory.length >= 2) {
        let lastPath = state.pathHistory[state.pathHistory.length - 2]
        let connections = getters.getPageConnections(lastPath)
        for (let i = 0; i < connections.length; i++) {
          let connection = connections[i]
          if(connection.path === currentPath) {
            navigate = true
            if (!state.overrideTransition) {
              commit('setTransitionType', connection.backwardTransition)
            }
            router.push(utils.langPath(lastPath, state.defaultLanguage))
            commit('setPathHistoryBack')
            dispatch('sendStoredState')
          }
        }
      }
      if (!navigate) {
        let backPathArray = []
        for (let j = 0; j < state.pages.length; j++) {
          let page = state.pages[j]
          for (let k = 0; k < page.connections.length; k++) {
            let connection = page.connections[k]
            if (connection.path === currentPath) {
              backPathArray.push({
                path: utils.removeLangPath(page.path, state.defaultLanguage),
                transition: connection.backwardTransition
              })
            }
          }
        }
        if (backPathArray.length > 0) {
          if (!state.overrideTransition) {
            commit('setTransitionType', backPathArray[0].transition)
          }
          router.push(utils.langPath(backPathArray[0].path, state.defaultLanguage))
          commit('setPathHistory', backPathArray[0].path)
          dispatch('sendStoredState')
        }
      }
    },
    setScore: function (context, n) {
      let state = context.state
      state.lms.setScore(n.raw, n.min, n.max, n.scaled)
    },
    setInteraction: function (context, n) {
      let state = context.state
      state.lms.setInteraction(n.id, n.result, n.description, n.type, n.correctResponse, n.correctId, n.learnerResponse, n.learnerId, n.weight)
    },
    setCourseCompletion: function (context) {
      let commit = context.commit
      commit('_setCourseCompletion', true)
      let state = context.state
      state.lms.setComplete()
    },
    initPathHistory: function (context, n) {
      let state = context.state
      let commit = context.commit
      let dispatch = context.dispatch
      state.lms.init()
      if (state.lms.isConnected()) {
        dispatch('loadStoredState')
      }
      if (state.pathHistory.length === 0) {
        if (n === '/') {
          commit('setPathHistory', n)
        } else {
          commit('setPathHistory', utils.removeLangPath(n, state.defaultLanguage))
        }
      }
    },
    closeCourse: function (context, n) {
      let state = context.state
      state.lms.exit()
      if (n) {
        window.top.close()
      }
    },
    loadStoredState: function (context) {
      let commit = context.commit
      let state = context.state
      let storedState = state.lms.getStoredState()
      if (storedState !== null) {
        commit('_setStoredCompletion', storedState.completion)
        commit('_setStoredHistory', storedState.history)
        commit('loadQuestionRecords', storedState.questionRecords)
        commit('loadQuizRecords', storedState.quizRecords)
        commit('_setCustomSuspendData', storedState.customSuspend)
        commit('setDefaultLanguage', storedData.defaultLanguage)
      }
      let bookmark = state.lms.getBookmark()
      if (bookmark.length > 0) {
        commit('_setBookmark',bookmark)
      }
    },
    sendStoredState: function (context) {
      let state = context.state
      let getters = context.getters
      let rootState = context.rootState
      let storedData = {}
      storedData.completion = state.completion
      storedData.history = state.pathHistory
      storedData.questionRecords = rootState.assessment.questionRecords
      storedData.quizRecords = rootState.assessment.quizRecords
      storedData.customSuspend = state.customSuspendData
      storedData.defaultLanguage = state.defaultLanguage
      state.lms.setStoredState(storedData)
      state.lms.setBookmark(getters.getPath)
    },
    gotoBookmark: function (context) {
      let state = context.state
      let dispatch = context.dispatch
      if (state.bookmark.length > 0) {
        if (state.bookmark === state.pathHistory[state.pathHistory.length - 1] && state.pathHistory.length > 1) {
          state.pathHistory.pop()
        }
        if(state.pathHistory.length === 1) {
          return
        }
        dispatch('navByPath', state.bookmark)
      } else{
        return
      }
    },
    setCompletionItem: function (context, n) {
      let state = context.state
      let commit = context.commit
      let getters = context.getters
      let dispatch = context.dispatch
      commit('_setCompletionItem', n)
      let items = getters.getCompletionItems
      let currentCompletion = getters.getPageCompletion()
      let completeTotal = 0
      for (let i = 0; i < items.length; i++) {
        completeTotal += items[i]
      }
      let completionObj = {}
      completionObj.path = state.pathHistory[state.pathHistory.length - 1]
      completionObj.complete = Math.max(completeTotal/items.length, currentCompletion)
      commit('_setPageCompletion', completionObj)
      let pagesCompletion = getters.getPagesCompletion
      if(pagesCompletion >= 1) {
        dispatch('setCourseCompletion')
      } else{
        dispatch('checkGroupCompletion')
      }
      dispatch('sendStoredState')
    },
    setPageCompletion: function (context, n) {
      let state = context.state
      let commit = context.commit
      let getters = context.getters
      let dispatch = context.dispatch
      let completionObj = {}
      completionObj.complete = 1
      if(arguments[1] !== undefined) {
        completionObj.path = n
      } else {
        completionObj.path = state.pathHistory[state.pathHistory.length - 1]
      }
      commit('_setPageCompletion', completionObj)
      let pagesCompletion = getters.getPagesCompletion
      if(pagesCompletion >= 1) {
        dispatch('setCourseCompletion')
      } else{
        dispatch('checkGroupCompletion')
      }
      dispatch('sendStoredState')
    },
    setDefaultPageCompletion: function (context) {
      let state = context.state
      let getters = context.getters
      let dispatch = context.dispatch
      let completion = getters.getCompletionItems
      if (completion.length === 0) {
        dispatch('setPageCompletion')
      }
    },
    checkGroupCompletion: function (context) {
      let state = context.state
      let getters = context.getters
      let dispatch = context.dispatch
      for (let i = 0; i < state.groups.length; i++) {
        let group = state.groups[i]
        if (group.complete) {
          let complete = getters.getGroupCompletion(group.title)
          if (complete >= 1) {
            dispatch('setCourseCompletion')
          }
        }
      }
    },
    setGroupCompletion: function (context, n) {
      let state = context.state
      let commit = context.commit
      let getters = context.getters
      let dispatch = context.dispatch
      let myGroup
      for (let i = 0; i < state.groups.length; i++) {
        let group = state.groups[i]
        if (group.title === n) {
          myGroup = group
          for (let j = 0; j < myGroup.pages.length; j++) {
            dispatch('setPageCompletion', myGroup.pages[j])
          }
          break
        }
      }
    },
    setSuspendItem: function (context, n) {
      let commit = context.commit
      let dispatch = context.dispatch
      commit('_setSuspendItem', n)
      dispatch('sendStoredState')
    },
    resetState: function (context) {
      let commit = context.commit
      commit('_setStoredHistory', ['/'])
      commit('clearCompletion')
      commit('clearCustomSuspend')
      commit('clearQuestionHistory')
      commit('clearQuizHistory')

    }
  },
  getters: {
    getCourseTitle: function (state) {
      return state.courseTitle
    },
    getPath: function (state) {
      return state.pathHistory[state.pathHistory.length - 1]
    },
    getLms: function (state) {
      return state.lms
    },
    isBookmarkAvailable: function (state) {
      if (state.lms.lmsConnected) {
        let location = state.lms.getBookmark()
        if (location && location.length > 0) {
          return true
        } else {
          return false
        }
      }
      return false
    },
    isGated: function (state) {
      return function (target) {
        let gated = false
        let from = ''
        let to = ''
        if (state.ignoreGating) {
          return false
        }
        if (typeof target === 'string') {
          from = null
          to = target
        } else{
          to = target.path
          from = target.location
        }
        for (let a = 0; a < state.pages.length; a++) {
          let path = utils.removeLangPath(state.pages[a].path, state.defaultLanguage)
          if (path === from || from === null) {
            let connections = state.pages[a].connections
            for (let b = 0; b < connections.length; b++) {
              if (connections[b].path === to) {
                let gate = connections[b].gate
                if (gate.indexOf('/') !== -1) {
                  //page
                  for (var c = 0; c < state.completion.length; c++) {
                    if (state.completion[c].path === gate) {
                      if(state.completion[c].complete !== 1) {
                        gated = true
                      }
                    }
                  }
                } else {
                  //group
                  for (var d = 0; d < state.groups.length; d++) {
                    if (state.groups[d].title === gate) {
                      let pages = state.groups[d].pages
                      let completionArray = []
                      for (var e = 0; e < pages.length; e++) {
                        let pagePath = pages[e]
                        for (var f = 0; f < state.completion.length; f++) {
                          if (state.completion[f].path === pagePath) {
                            if (state.completion[f].complete < 1) {
                              gated = true
                            }
                          }
                        }
                      }
                      return gated
                    }
                  }
                }
              }
            }
          }
        }
        return gated
      }
    },
    getDefaultLanguage: function (state) {
      return state.defaultLanguage
    },
    getAvailableLanguages: function (state) {
      return state.availableLanguages
    },
    getTransitionType: function (state) {
      return state.transitionType
    },
    getCompletionItem: function (state) {
      return function (itemIndex) {
        for (var i = 0; i < state.completion.length; i++) {
          if(state.completion[i].path === state.pathHistory[state.pathHistory.length - 1]) {
            if (itemIndex < state.completion[i].completionItems.length) {
              return state.completion[i].completionItems[itemIndex]
            } else{
              return null
            }
          }
        }
        return null
      }
    },
    getCompletionItems: function (state) {
      for (var i = 0; i < state.completion.length; i++) {
        if (state.completion[i].path === state.pathHistory[state.pathHistory.length - 1]) {
          return state.completion[i].completionItems
        }
      }
      return null
    },
    getPageCompletion: function (state) {
      return function () {
        let path = (arguments.length > 0)? arguments[0]: state.pathHistory[state.pathHistory.length - 1]
        for (var i = 0; i < state.completion.length; i++) {
          if (state.completion[i].path === path) {
            return state.completion[i].complete
          }
        }
        return null
      }
    },
    getPagesCompletion: function (state) {
      let total = 0
      for (var i = 0; i < state.completion.length; i++) {
        total += state.completion[i].complete
      }
      return total/state.completion.length
    },
    getGroupCompletion: function (state) {
      return function (title) {
        for (var i = 0; i < state.groups.length; i++) {
          if (state.groups[i].title === title) {
            let pages = state.groups[i].pages
            let completionArray = []
            for (var j = 0; j < pages.length; j++) {
              let pagePath = pages[j]
              for (var k = 0; k < state.completion.length; k++) {
                if (state.completion[k].path === pagePath) {
                  completionArray.push(state.completion[k].complete)
                }
              }
            }
            let size = completionArray.length
            let sum = 0
            for (let a = 0; a < completionArray.length; a++) {
              sum += completionArray[a]
            }
            return sum/size
          }
        }
        return null
      }
    },
    getCourseCompletion: function (state) {
      return state.courseComplete
    },
    getPage: function (state) {
      return function () {
        let path = (arguments.length > 0)? arguments[0]: state.pathHistory[state.pathHistory.length - 1]
        for (var i = 0; i < state.pages.length; i++) {
          if (utils.removeLangPath(state.pages[i].path, state.defaultLanguage) === path) {
            return utils.clonePage(state.pages[i], state.defaultLanguage)
          }
        }
        return null
      }
    },
    getPages: function (state) {
      let pageArray = []
      for (var j = 0; j < state.completion.length; j++) {
        let pagePath = state.completion[j].path
        for (var k = 0; k < state.pages.length; k++) {
          if (utils.removeLangPath(state.pages[k].path, state.defaultLanguage) === pagePath) {
            pageArray.push(utils.clonePage(state.pages[k], state.defaultLanguage))
          }
        }
      }
      return pageArray
    },
    getGroup: function (state) {
      return function (name) {
        for (var i = 0; i < state.groups.length; i++) {
          let group = state.groups[i]
          if(group.title === name) {
            return group
          }
        }
        return null
      }
    },
    getGroups: function (state) {
      return function () {
        let name = (arguments.length > 0)? arguments[0]: ''
        let groupArray = []
        for (var i = 0; i < state.groups.length; i++) {
          let group = state.groups[i]
          if(group.title.indexOf(name) !== -1) {
            groupArray.push(group)
          }
        }
        return groupArray
      }
    },
    getGroupPages: function (state) {
      return function (name) {
        for (var i = 0; i < state.groups.length; i++) {
          let group = state.groups[i]
          if(group.title === name) {
            let pageArray = []
            for (var j = 0; j < group.pages.length; j++) {
              let pagePath = group.pages[j]
              for (var k = 0; k < state.pages.length; k++) {
                if (utils.removeLangPath(state.pages[k].path, state.defaultLanguage) === pagePath) {
                  pageArray.push(utils.clonePage(state.pages[k], state.defaultLanguage))
                }
              }
            }
            return pageArray
          }
        }
        return null
      }
    },
    getGroupsPages: function (state) {
      return function () {
        let name = (arguments.length > 0)? arguments[0]: ''
        let groupsArray = []
        for (var i = 0; i < state.groups.length; i++) {
          let group = state.groups[i]
          if(group.title.indexOf(name) !== -1) {
            let pageArray = []
            for (var j = 0; j < group.pages.length; j++) {
              let pagePath = group.pages[j]
              for (var k = 0; k < state.pages.length; k++) {
                if (utils.removeLangPath(state.pages[k].path, state.defaultLanguage) === pagePath) {
                  pageArray.push(utils.clonePage(state.pages[k], state.defaultLanguage))
                }
              }
            }
            groupsArray.push(pageArray)
          }
        }
        return groupsArray
      }
    },
    getGroupOfPage: function (state) {
      return function () {
        let name = (arguments.length > 0)? arguments[0]: ''
        let path = state.pathHistory[state.pathHistory.length - 1]
        for (var i = 0; i < state.groups.length; i++) {
          let group = state.groups[i]
          if (group.title.indexOf(name) !== -1) {
            if (group.pages.includes(path)) {
              return group.title
            }
          }
        }
        return ''
      }
    },
    getIgnoreGating: function (state) {
      return state.ignoreGating
    },
    getOverrideTransition: function (state) {
      return state.overrideTransition
    },
    getPageConnections: function (state) {
      return function () {
        let path = (arguments.length > 0)? arguments[0]: state.pathHistory[state.pathHistory.length - 1]
        for (var i = 0; i < state.pages.length; i++) {
          if (utils.removeLangPath(state.pages[i].path, state.defaultLanguage) === path) {
            return state.pages[i].connections
          }
        }
        return null
      }
    },
    getPageConnectionsAsPages: function (state) {
      return function () {
        let path = (arguments.length > 0)? arguments[0]: state.pathHistory[state.pathHistory.length - 1]
        for (let i = 0; i < state.pages.length; i++) {
          if (utils.removeLangPath(state.pages[i].path, state.defaultLanguage) === path) {
            let pageArray = []
            for (let j = 0; j < state.pages[i].connections.length; j++) {
              let connectionPath = state.pages[i].connections[j].path
              for (let k = 0; k < state.pages.length; k++) {
                if (utils.removeLangPath(state.pages[k].path, state.defaultLanguage) === connectionPath) {
                  pageArray.push(utils.clonePage(state.pages[k], state.defaultLanguage))
                }
              }
            }
            return pageArray
          }
        }
        return null
      }
    },
    hasSuspendItem: function (state) {
      return function () {
        let name = ''
        if (arguments.length > 0) {
          name = arguments[0]
        } else {
          return null
        }
        return state.customSuspendData.hasOwnProperty(name)
      }
    },
    getSuspendItem: function (state) {
      return function () {
        let name = ''
        if (arguments.length > 0) {
          name = arguments[0]
        } else {
          return null
        }
        if (state.customSuspendData.hasOwnProperty(name)) {
          return state.customSuspendData[name]
        }
      }
    },
    getString: function (state) {
      return function () {
        let obj = ''
        if (arguments.length > 0) {
          obj = arguments[0]
        } else {
          return null
        }
        let lang = state.defaultLanguage.substr(1)
        return state.strings[lang][obj.category][obj.name]
      }
    }
  },
  mutations: {
    setPathHistory: function (state, n) {
      state.pathHistory.push(n)
    },
    setPathHistoryBack: function (state) {
      state.pathHistory.pop()
    },
    setDefaultLanguage: function (state, n) {
      if (n.indexOf('/') > -1) {
        state.defaultLanguage = n
      } else {
        state.defaultLanguage = '/' + n
      }
    },
    setTransitionType: function (state, n) {
      state.transitionType = n
    },
    setIgnoreGating: function (state, n) {
      state.ignoreGating = n
    },
    setOverrideTransition: function (state, n) {
      state.overrideTransition = n
    },
    clearCompletion: function (state) {
      for (var i = 0; i < state.completion.length; i++) {
        var completionItem = state.completion[i]
        completionItem.complete = 0
        for (var j = 0; j < completionItem.completionItems.length; j++) {
          completionItem.completionItems[j] = 0
        }
      }
    },
    clearCustomSuspend: function (state) {
      state.customSuspendData = {}
    },
    _setCourseCompletion: function (state) {
      state.courseComplete = true
    },
    _setCompletionItem: function (state, n) {
      for (let i = 0; i < state.completion.length; i++) {
        if (state.completion[i].path === state.pathHistory[state.pathHistory.length-1]) {
          if (n.index < state.completion[i].completionItems.length) {
            state.completion[i].completionItems.splice([n.index], 1, n.value)
          }
        }
      }
    },
    _setPageCompletion: function (state, n) {
      for (var i = 0; i < state.completion.length; i++) {
        if (state.completion[i].path === n.path) {
          state.completion[i].complete = n.complete
        }
      }
    },
    _setStoredCompletion: function (state, n) {
      state.completion = n
    },
    _setBookmark: function (state, n) {
      state.bookmark = n
    },
    _setStoredHistory: function (state, n) {
      state.pathHistory = n
    },
    _setCustomSuspendData: function (state, n) {
      state.customSuspendData = n
    },
    _setSuspendItem: function (state, n) {
      state.customSuspendData[n.name] = n.value
    },
  }
}